import { orderBy } from "lodash";
import { OrderBy, PunkSortBy } from "../__generated__/globalTypes";
import { Punk } from "./types";

export abstract class FilterUtil {
  static sortPunks(sort: PunkSortBy, order: OrderBy, punks?: Maybe<Punk[]>): Maybe<Punk[]> {
    if (!punks || !sort || !order) {
      return punks;
    }
    switch (sort) {
      case PunkSortBy.ID:
        return orderBy(
          punks,
          (p: Punk) => Number(p.id),
          order === OrderBy.ASC ? 'asc' : 'desc',
        )
      case PunkSortBy.RANK:
        return orderBy(
          punks,
          (p: Punk) => p.rank,
          order === OrderBy.ASC ? 'asc' : 'desc',
        )
      default:
        return punks;
    }
  }
}