import { BigNumber } from 'bignumber.js';
import { CommonConstants } from '../common/constants';
import { CommonUtil } from './common.util';
import { PunkEvent, TypeNames } from './types';

export abstract class TransformUtil {
  static get defaultBigNumber(): BigNumber {
    return new BigNumber("0");
  }

  static toBigNumber(number: string): BigNumber {
    return TransformUtil.toMaybeBigNumber(number) ?? TransformUtil.defaultBigNumber;
  }

  static toMaybeBigNumber(number: Maybe<string>): Maybe<BigNumber> {
    if (!number || (CommonUtil.isNullOrWhitespace(number) && !CommonUtil.isNumber(number))) {
      return undefined;
    }
    return new BigNumber(number);
  }

  static toAddress(value: string): Maybe<string> {
    if (CommonUtil.isNullOrWhitespace(value) || value === CommonConstants.ZERO_ADDRESS) {
      return undefined;
    }
    return value;
  }

  static toTimestamp(date: Maybe<Date>): string {
    if (date) {
      return Math.floor(date.getTime() / 1000).toString();
    }
    return "0";
  }

  static toPunkEvent(event: any): PunkEvent {
    return {
      __typename: event.__typename ?? TypeNames.PunkEvent,
      id: event.id ?? `${event.accountBlockHash}-${event.accountBlockHeight}-${event.eventType}`,
      accountBlockHash: event.accountBlockHash,
      accountBlockHeight: TransformUtil.toBigNumber(event.accountBlockHeight),
      timestamp: TransformUtil.toBigNumber(event.timestamp),
      eventType: Number(event.eventType),
      punkId: event.punkId,
      amount: TransformUtil.toBigNumber(event.amount),
      fromAddress: TransformUtil.toAddress(event.fromAddress) ?? '',
      toAddress: TransformUtil.toAddress(event.toAddress) ?? ''
    };
  }
}