import { Box, CardActionArea, Chip, styled } from '@mui/material';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { CommonConstants } from '../../../../common/constants';
import { Punk } from '../../../../util/types';
import { UrlUtil } from '../../../../util/url.util';

const CustomCardMedia = styled('div')(({ theme }) => (`
  height: 320px;
  // animation: lightning 4s ease-out infinite;
  background-image: url('${CommonConstants.ITEM_BG_URL}');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
`));

const PunkImage = styled('div')(
  ({ theme }) => `
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  image-rendering: pixelated;
`,
);

interface Props {
  item: Punk
}

export const MarketplaceBrowseItem: React.FC<Props> = (props: Props) => {

  return (
    <Card>
      <CardActionArea component={RouterLink} to={`/punk/${props.item.id}`}>
        <CustomCardMedia>
          <PunkImage sx={{ backgroundImage: `url('${UrlUtil.getPunkUrl(props.item.image)}')` }} />
        </CustomCardMedia>
        <CardContent sx={{ p: 0 }}>
          <Typography variant="h6" component="div" sx={{ p: 1 }}>
            {CommonConstants.PUNK_NAME} #{props.item.id}
          </Typography>
          <Box sx={{ height: "70px", overflow: "auto" }}>
            <Box sx={{ p: 1 }}>
              {props.item.attributes?.map((a: string) => (
                <Chip key={a} label={a} variant="outlined" sx={{ mb: 1, mr: 1 }} size="small" />
              ))}
            </Box>
          </Box>
        </CardContent>
      </CardActionArea>
    </Card>
  )
}
